"use client";

import "./organization.css";
import { useParams, useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { decodeId } from "../../../lib/utils/secureId";

export default function OrganizationDetailsPage() {
  const { id } = useParams();
  const router = useRouter();

  const [org, setOrg] = useState(null);
  const [loading, setLoading] = useState(true);

  const realOrgId = decodeId(id);

  /* SAFETY */
  useEffect(() => {
    if (!realOrgId) {
      router.replace("/");
    }
  }, [realOrgId]);

  /* FETCH ORGANIZATION */
  useEffect(() => {
    if (!realOrgId) return;

    const fetchOrg = async () => {
      try {
        const res = await getOrganizationProfileApi(realOrgId);
        if (res?.status) {
          setOrg(res.data);
        } else {
          router.replace("/");
        }
      } catch {
        router.replace("/");
      } finally {
        setLoading(false);
      }
    };

    fetchOrg();
  }, [realOrgId]);

  if (loading) return null;

  return (
    <div className="container my-4 org-page">

      {/* ===== HERO BANNER ===== */}
      <div className="org-hero position-relative rounded-4 overflow-hidden">
        <img
          src={org?.bannerImage || "/images/event.png"}
          className="w-100 org-hero-img"
          alt={org?.organizationName || "Organization"}
        />

        <span className="badge bg-purple position-absolute top-0 start-0 m-3">
          Upcoming Event
        </span>

        <div className="org-hero-dots">
          <span className="dot active"></span>
          <span className="dot"></span>
          <span className="dot"></span>
          <span className="dot"></span>
        </div>
      </div>

      {/* ===== ORG DETAILS CARD ===== */}
      <div className="org-info-card shadow-sm rounded-4 p-4 mt-4 d-flex justify-content-between align-items-center flex-wrap gap-3">

        <div className="d-flex align-items-center gap-3">
          {org?.logoUrl ? (
            <img src={org.logoUrl} className="org-logo" />
          ) : (
            <div
              className="org-logo d-flex align-items-center justify-content-center"
              style={{ background: "#1c1c1c", color: "#fff", fontWeight: 700 }}
            >
              {org?.organizationName?.charAt(0) || "O"}
            </div>
          )}

          <div>
            <h5 className="mb-1 fw-bold">
              {org?.organizationName || "Organization Name"}
            </h5>

            <div className="org-meta">
              <span>üéü {org?.totalEvents || 0} Events</span>
              <span>‚≠ê {org?.rating || "0.0"} / {org?.reviewCount || 0} reviews</span>
              <span>üèÜ {org?.rank ? `${org.rank} Rank` : "‚Äî"}</span>
            </div>
          </div>
        </div>

        <button className="btn btn-purple px-4 rounded-pill">
          + Follow
        </button>
      </div>

      {/* ===== UPCOMING EVENTS ===== */}
      <section className="mt-5">
        <h5 className="fw-bold mb-3">Upcoming Events</h5>

        <div className="row g-4">
          {(org?.upcomingEvents || []).length > 0 ? (
            org.upcomingEvents.map((ev, i) => (
              <div className="col-xl-3 col-lg-4 col-md-6" key={i}>
                <div className="card h-100">
                  <img
                    src={ev.bannerImage || "/images/event.png"}
                    className="card-img-top"
                  />
                  <div className="card-body">
                    <h6 className="text-truncate">
                      {ev.title || "Event Title"}
                    </h6>
                    <p className="small mb-1">
                      üìç {ev.city || "N/A"}
                    </p>
                    <p className="small mb-1">
                      üìÖ {ev.eventDate || "TBA"}
                    </p>
                    <span className="badge badge-paid">
                      {ev.isPaid ? "Paid" : "Free"}
                    </span>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <p className="text-muted">No upcoming events</p>
          )}
        </div>
      </section>

      {/* ===== PAST EVENTS ===== */}
      <section className="mt-5">
        <h5 className="fw-bold mb-3">Past Events</h5>

        <div className="row g-4">
          {(org?.pastEvents || []).length > 0 ? (
            org.pastEvents.map((ev, i) => (
              <div className="col-lg-3 col-md-6" key={i}>
                <div className="past-card">
                  <img src={ev.bannerImage || "/images/event.png"} />
                  <div className="past-overlay">
                    <h6>{ev.title || "Event"}</h6>
                    <span>{ev.views || 0} views</span>
                  </div>
                </div>
              </div>
            ))
          ) : (
            <p className="text-muted">No past events</p>
          )}
        </div>
      </section>

    </div>
  );
}
